/* Rating Button Svyles*/
.btn-primary {
  font-size: 5vmin;
  font-weight: bold;
  background-color: #3f51b5;
  border-color: #3f51b5;
  border-radius: 4px;
  height: 30vmin;
  width: 9%;
  margin: 0.5%;
  padding-left: 3px;
  padding-right: 3px;
  overflow: hidden;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12);
}

.btn-primary:focus,
.btn-primary.focus {
  background-color: #007bff;
  border-color: #007bff;
}

p.code-desc {
  font-size: 3vmin;
  font-weight: normal;
  line-height: 1;
}

/* Video Player Styles */
.player {
  padding-top: 2.5vmin;
}

/* iPad Video Player Styles */
@media (min-width: 1024px) {

  .player {
    height: 60vmin !important;
  }
}

/* Form Styles */
div.MuiFormControl-root {
  width: 100%;
}
